@import url(https://s3.amazonaws.com/zaius-images/enpcyfSlfukmvIC8Ff-CCA/_/fonts/BrandonGrotesque-Medium.css);
@import url(https://s3.amazonaws.com/zaius-images/enpcyfSlfukmvIC8Ff-CCA/_/fonts/BrandonGrotesque-Light.css);
.filter-chip{flex:0 0 auto;margin:5px 15px;white-space:nowrap;font-family:"BrandonGrotesque-Medium", Arial, sans-serif;line-height:2.5}.filter-chip--active{margin:5px 5px;background-color:#F5F3ED;border-radius:16px;outline:none;border:none}.filter-chip--active span{padding-left:12px;padding-right:12px}.filter-chip__container:last-of-type{padding-right:30px}

.filter-bar{width:100%;max-width:1024px;margin:auto}.filter-bar__container{display:flex;flex-direction:column;max-width:1024px}.filter-bar div{display:flex}

.item-unit{margin:5px;max-width:170px;position:relative}.item-unit img{width:12vw;border-radius:10%;min-width:150px;max-width:170px}.item-unit span{cursor:pointer}.item-unit__favourite,.item-unit__description{position:absolute;visibility:hidden}.item-unit__favourite{right:5px;top:2px}.item-unit__favourite--bookmarked{position:absolute;right:7px;top:10px}.item-unit__favourite--bookmarked :hover{display:none}.item-unit__description{left:10px;bottom:15px;width:90%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.item-unit:hover .item-unit__favourite,.item-unit:hover .item-unit__description{visibility:visible}

.items__container{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;margin:0 5px;max-width:1080px}.items__container--main{border-top:1px solid #DDD;padding-top:10px;display:flex;justify-content:center}

body{margin:unset;font-family:"BrandonGrotesque-Light", Arial, sans-serif}

